<template>
    <div class="Peliculas">
        <div class="alert alert-primary d-flex align-items-center mt-5" role="alert" id="alerta">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-film"
                viewBox="0 0 16 16">
                <path
                    d="M0 1a1 1 0 0 1 1-1h14a1 1 0 0 1 1 1v14a1 1 0 0 1-1 1H1a1 1 0 0 1-1-1V1zm4 0v6h8V1H4zm8 8H4v6h8V9zM1 1v2h2V1H1zm2 3H1v2h2V4zM1 7v2h2V7H1zm2 3H1v2h2v-2zm-2 3v2h2v-2H1zM15 1h-2v2h2V1zm-2 3v2h2V4h-2zm2 3h-2v2h2V7zm-2 3v2h2v-2h-2zm2 3h-2v2h2v-2z" />
            </svg>
            <div id="msjAlert">A continuación hay un listado con los nombres de las peliculas</div>
        </div>
        <select class="form-select" id="selectPeliculas" @change="onChange($event)">
            <option selected>Selecciona...</option>
            <option v-for="opt in arrayPeliculas" :value="opt.id">{{ opt.title }}</option>
        </select>
        <div class="card mt-5" v-if="mostrarCard == true">
            <img v-bind:src="foto" class="card-img-top" v-bind:alt="tituloI">
            <div class="card-body">
                <h3 class="card-title"><strong>Titulo Originial: </strong>{{ tituloR }}</h3>
                <h4 class="card-title"><strong>Titulo en ingles: </strong>{{ tituloI }}</h4>
                <p class="card-text"><strong>Descripción </strong>{{ descripcion }}</p>
                <div class="marco">
                    <p><strong>Director: </strong>{{ director }}</p>
                </div>
                <div class="marco mt-2">
                    <p><strong>Duración: </strong>{{ duracion }} minutos</p>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
export default {
    name: 'Peliculas',
    props: {
    },
    data: function () {
        return {
            arrayPeliculas: {
                type: Array
            },
            tituloR: '',
            tituloI: '',
            foto: '',
            director: '',
            descripcion: '',
            duracion: '',
            mostrarCard: false

        }
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData: function () {
            fetch('https://ghibliapi.vercel.app/films')
                .then(response => response.json())
                .then(datos => {
                    this.arrayPeliculas = datos;
                })
        },
        onChange: function (event) {
            let id = event.target.value;
            let objeto = this.arrayPeliculas.find(function (obj) {
                return obj.id === id;
            });
            this.tituloI = objeto.title;
            this.tituloR = objeto.original_title_romanised;
            this.foto = objeto.image;
            this.director = objeto.director;
            this.descripcion = objeto.description;
            this.duracion = objeto.running_time;
            this.mostrarCard = true;
        }
    }
}
</script>
  
<style scoped>
#alerta,
select {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}

#msjAlert {
    margin-left: 10px;
    color: black;
    font-weight: 500;
}

.card {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
}

.marco {
    border: 0.5px solid rgb(186, 186, 186);
}
</style>