<template>
    <div class="Login">
        <button v-if="mostrarInicio==true" type="button" class="btn btn-success" v-on:click="mostrarForm" id="btnDatos">INGRESAR DATOS</button>
        <form v-if="mostrar==true" ref="formulario">
            <label for="nombre" class="form-label">NOMBRE:</label>
            <input type="text" class="form-control" id="nombre" placeholder="INGRESE NOMBRE" v-model="nombre"><br>
            <label for="apellido" class="form-label">APELLIDO:</label>
            <input type="text" class="form-control" id="apellido" placeholder="INGRESE APELLIDO" v-model="apellido"><br>
            <button type="button" class="btn btn-success" v-on:click="mostrarInfo" >INGRESAR</button>
        </form>
        <div class="tarjeta" v-if="mostrarTarjeta==true">
            <h1>Bienvenid@ {{ getFullName }}</h1>
            <p>Aquí podrás encontrar información de peliculas de anime de Studio Ghibli</p>
            <button type="button" class="btn btn-warning mt-5" id="btnLimpiar" v-on:click="limpiarBusqueda">LIMPIAR INFORMACIÓN</button>
        </div>

        <div class="movies" v-if="mostrarPeliculas==true">
            <Peliculas/>
        </div>
    </div>
</template>
  
<script>
import Peliculas from './Peliculas.vue'
export default {
    name: 'Login',
    components: {
        Peliculas
    },
    data:function(){
        return{
            mostrar: false,
            mostrarTarjeta: false,
            mostrarPeliculas:false,
            mostrarInicio: true,
            nombre:'',
            apellido: ''
        }
    },
    methods:{
        mostrarForm: function(){
            this.mostrar = true;
            this.mostrarInicio = false;
        },
        mostrarInfo: function(){
            let nom = document.getElementById('nombre').value;
            let ape = document.getElementById('apellido').value;
            this.nombre = nom;
            this.apellido = ape;
            this.mostrar = false;
            this.mostrarTarjeta = true;
            this.mostrarPeliculas = true;
        },
        limpiarBusqueda: function(){
            this.mostrar = false;
            this.mostrarTarjeta = false;
            this.mostrarPeliculas = false;
            this.mostrarInicio = true;
            this.nombre = '';
            this.apellido = '';
        }
    },
    computed:{
        getFullName(){
            let retorno = this.nombre + ' ' + this.apellido;
            return retorno;
        }
    }
}
</script>
  
<style scoped>
form{
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}
.tarjeta{
    width: 30%;
    height: 220px;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid black;
}
#btnLimpiar{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}
</style>
  